<template>
    <div class="backpack">
        <a-button @click="dissolveAll" type="text" class="dissolveAll">
            批量分解
        </a-button>
        <a-menu
            :selectedKeys="selectedKeys"
            mode="horizontal"
            :items="menuList"
            @click="menuClick"
        />
        <div class="content">
            <div
                v-for="(item, index) in backpack"
                class="items"
                :class="{ choose: item.isChoose }"
                @click="itemChoose(item, index)"
            >
                <p class="name">
                    <Iconfont type="wx-attacker1" v-if="item.type === '2'">
                    </Iconfont>
                    <Iconfont
                        type="wx-clothes"
                        v-if="item.type === '3'"
                    ></Iconfont>
                    <Iconfont
                        type="wx-jiezhi1"
                        v-if="item.type === '4'"
                    ></Iconfont>
                    {{ item.name }}
                </p>
                <p>
                    <span v-for="item in item.attr" class="attr">
                        <span class="br"> - </span>
                        <span>{{ item.nameText }}: {{ item.desc }}</span>
                    </span>
                </p>
                <p class="status">
                    <a-button
                        v-if="item.status === '1'"
                        type="primary"
                        @click="useItem(item)"
                    >
                        {{ $t("use") }}
                    </a-button>
                    <a-button
                        v-if="item.status === '2'"
                        type="primary"
                        @click="equipItem(item)"
                    >
                        {{ $t("equip") }}
                    </a-button>
                    <a-button
                        v-if="item.status === '2'"
                        type="primary"
                        style="margin-left: 10px"
                        @click="dissolveItem(item)"
                    >
                        {{ $t("dissolve") }}
                    </a-button>
                    <a-button v-if="item.status === '21'" type="text" disabled>
                        E
                    </a-button>
                    <a-button
                        v-if="item.status === '11'"
                        type="primary"
                        disabled
                    >
                        {{ $t("used") }}
                    </a-button>
                    <a-button
                        v-if="item.status === '21'"
                        type="primary"
                        style="margin-left: 5px"
                        @click="unEquipItem(item)"
                    >
                        {{ $t("equiped") }}
                    </a-button>
                </p>
            </div>
            <a-result :title="$t('noItem')" v-if="backpack.length === 0">
                <template #icon></template>
            </a-result>
        </div>
    </div>
</template>

<script>
import {
    reactive,
    toRefs,
    onMounted,
    ref,
    h,
    watch,
    computed,
    createVNode,
} from "vue";
import backpackApi from "@/assets/api/backpackApi";
import backpackMenu from "@/assets/config/backpackMenu.js";
import { useI18n } from "vue-i18n";
import { transItemText, transItemType } from "@/assets/common/transItemText";
import { SmileTwoTone } from "@ant-design/icons-vue";
import Iconfont from "@/components/Iconfont.vue";
import { message, Modal } from "ant-design-vue";
import fightApi from "@/assets/api/fightApi";

export default {
    name: "Backpack", // 背包
    components: { Iconfont },
    setup() {
        const { t, locale } = useI18n();
        const state = reactive({
            menuList: backpackMenu,
            selectedKeys: ["all"],
            backpack: [],
        });

        onMounted(async () => {
            // 根据语言修改 menu label
            changeLocaleLabel();
            // 获取背包信息
            await getBackpackInfo();
        });

        // 获取背包信息
        const getBackpackInfo = async () => {
            let res = await backpackApi.getInfo({
                uid: sessionStorage.getItem("uid"),
                type: state.selectedKeys[0],
            });
            state.backpack = res.data.map((i) => {
                return {
                    ...i,
                    attr: transItemText(i.attr),
                    typeText: transItemType(i.type),
                    isChoose: false,
                };
            });
        };

        // 选中道具
        const itemChoose = async (item, index) => {
            state.backpack[index].isChoose = !state.backpack[index].isChoose;
        };

        // 选中道具的数组
        const itemChooseList = computed(() => {
            return state.backpack.filter((i) => i.isChoose);
        });

        // 提交全部分解
        const dissolveAll = async () => {
            if (itemChooseList.value.length === 0) {
                message.error("请选中道具！");
                return;
            }
            //  二次确认
            Modal.confirm({
                title: `是否分解全部 ${itemChooseList.value.length}件 道具?`,
                content: createVNode("div", { style: "color:red;" }),
                okText: "确定",
                cancelText: "取消",
                async onOk() {
                    itemChooseList.value.forEach((i) => {
                        dissolveItem(i);
                    });
                },
            });
        };

        // 提交分解装备
        const dissolveItem = async (item) => {
            console.log(item);
            Modal.confirm({
                title: `是否分解 ${item.name}?`,
                content: createVNode("div", { style: "color:red;" }),
                okText: "确定",
                cancelText: "取消",
                async onOk() {
                    itemChooseList.value.forEach((i) => {
                        dissolveItem(i);
                    });
                },
            });

            let res = await backpackApi.postDissolveItem({
                id: item.id,
                uid: sessionStorage.getItem("uid"),
            });

            // 获取背包信息
            await getBackpackInfo();
        };

        // 提交装备道具
        const equipItem = async (item) => {
            let res = await backpackApi.postEquipItem({
                id: item.id,
                uid: sessionStorage.getItem("uid"),
                type: item.type,
            });

            // 获取背包信息
            await getBackpackInfo();
        };

        // 提交卸下道具
        const unEquipItem = async (item) => {
            let res = await backpackApi.postUnEquipItem({
                id: item.id,
                uid: sessionStorage.getItem("uid"),
                type: item.type,
            });

            // 获取背包信息
            await getBackpackInfo();
        };

        // 提交使用道具
        const useItem = async (item) => {
            let res = await backpackApi.postUseItem({
                id: item.id,
            });

            // 获取背包信息
            await getBackpackInfo();
        };

        // 菜单点击
        const menuClick = async (item, key, keyPath) => {
            // 当前选中项
            state.selectedKeys = [item.key];
            // 获取背包信息
            await getBackpackInfo();
        };

        // 监听语言改变
        watch(
            locale,
            () => {
                // 根据语言修改 menu label
                changeLocaleLabel();
            },
            { deep: true }
        );

        // 根据语言修改 menu label
        const changeLocaleLabel = () => {
            for (let key in state.menuList) {
                let text = t(state.menuList[key].key);
                state.menuList[key].label = text;
                state.menuList[key].title = text;
            }
        };

        return {
            ...toRefs(state),
            itemChoose,
            equipItem,
            unEquipItem,
            useItem,
            menuClick,
            SmileTwoTone,
            dissolveItem,
            dissolveAll,
        };
    },
};
</script>

<style lang="scss" scoped>
.backpack {
    margin: 10px;
    position: relative;

    .dissolveAll {
        position: absolute;
        right: 0;
        top: 0;
        height: 46px;
    }

    .content {
        margin-top: 10px;
        overflow: hidden;

        .items {
            float: left;
            border-radius: 8px;
            padding: 10px 10px 0;
            border: 1px solid #d2d2d2;
            margin-right: 10px;
            margin-bottom: 10px;
            cursor: pointer;

            .br {
                padding: 0 5px;
            }

            .status {
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }

        .choose {
            border: 1px solid deepskyblue;
        }
    }
}
</style>
